  line-height: 24px;
}

a {
  color: #ff00d4;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

canvas {
  display: block;
}

#info {
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  pointer-events: none;
  z-index: 1;
  padding: 10px;
  position: absolute;
  top: 0;
}

#bottom-info {
  width: 100%;
  box-sizing: border-box;
  text-align: left;
  -webkit-user-select: none;
  user-select: none;
  pointer-events: none;
  z-index: 1;
  padding: 10px;
  position: absolute;
  bottom: 0;
}

a, button, input, select {
  pointer-events: auto;
}

.dg.ac {
  -webkit-user-select: none;
  user-select: none;
  z-index: 2 !important;
}

.overlay {
  z-index: 2;
  width: 100%;
  height: 100%;
  opacity: 1;
  color: #fff;
  background-color: #000;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.overlay > div {
  text-align: center;
}

.overlay > div > button {
  height: 20px;
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  outline: 1px solid #fff;
}

.overlay > div > p {
  color: #777;
  font-size: 30px;
}

.spinner {
  width: 100px;
  height: 100px;
  z-index: 2;
  background-color: silver;
  border-radius: 100%;
  animation: sk-scaleout 1s ease-in-out infinite;
  position: absolute;
  top: 60%;
}

@-webkit-keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(1);
  }
}

@keyframes sk-scaleout {
  0% {
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

